import request from '@/utils/request'

// 订单结算
export const checkOrder = (mode, obj) => {
  return request({
    url: '/checkout/order',
    params: {
      mode, // 判断是否为cart 或者 buyNow
      delivery: 10,
      shopId: 0,
      couponId: 0,
      ...obj
    }
  })
}

// 提交订单
export const submitOrder = (mode, obj) => {
  return request({
    url: '/checkout/submit',
    method: 'POST',
    data: {
      ...obj,
      delivery: 10,
      couponId: 0,
      isUsePoints: -1,
      mode,
      payType: 10
    }
  })
}

// 我的订单列表
export const getMyOrderList = (dataType, page) => {
  return request({
    url: '/order/list',
    params: {
      dataType,
      page
    }
  })
}
